.cdr-button {
  color: lightgray;
  background-color: blue;
  border: 1px solid yellow;
  padding: 10px
}
.cdr-button:hover,
  .cdr-button:active,
  .cdr-button:focus {
  color: white;
  background-color: black
}
.cdr-button[disabled] {
  color: black;
  background-color: lightgray
}



